//timer based on alsoute time
function CountDownTimer(dt) {
    var end = new Date(dt);
    var _second = 1000;
    var _minute = _second * 60;
    var _hour = _minute * 60;
    var _day = _hour * 24;
    var timer;
    function showRemaining() {
        var now = new Date();
        var distance = end - now;
        if (distance < 0) {
            clearInterval(timer);
            eachTimer();
            settimeleftText((timeleftText)=>"Time over");
            finish();
            return;
        }
        var days = Math.floor(distance / _day);
        var hours = Math.floor((distance % _day) / _hour);
        var minutes = Math.floor((distance % _hour) / _minute);
        var seconds = Math.floor((distance % _minute) / _second);
        
        settimeleftText((timeleftText)=>"Time left... ");
        settimeleftText((timeleftText)=>timeleftText + minutes + " : " + seconds);

        
    }
    timer = setInterval(showRemaining, 1000);
}

function eachTimer() {
    let newtime = new Date();
    let timediff = newtime - currenttime;
    currenttime = newtime;
    let sumtime = timepassed+timediff;
    timepassed = sumtime;
    solvingtimeByQuestion[currentround] += timediff;
    console.log("time spent for Q" + (currentround+1) + " : " +solvingtimeByQuestion[currentround]);
}

