import React from 'react';
import Axios from 'axios';
import { render } from "react-dom";
import { Link } from "react-router-dom";
import { useState } from "react";

function RegisterLogin(props) {

    const [logpage, setlogpage] = useState(true);
    const [admin, setadmin] = useState(false);

    let ID = "";
    let PW = "";
  
    let loginID = "";
    let loginPW = "";
  
    const register = () => {
      Axios.post('http://localhost:3001/api/insert', {ID: ID , PW: PW
    }).then((e) => {
      alert(e.data);
      console.log(e.data);
    });
    };
  
  
    const login = () => {
      Axios.post('http://localhost:3001/api/login', {loginID: loginID , loginPW: loginPW
    }).then((e) => {
      alert(e.data);
      console.log(e.data);
      if (e.data == "Administrator login") {
        logined();
        setadmin((admin) => !admin);
      }
      else if (e.data == "Sucessfully login") {
        logined();
      } else {console.log("not logined");}
    });
    };

    function logined() {
        setlogpage((logpage) => !logpage);
    }



    return (
        <>
<div style={{ display: admin ? "none" : "block" }} className='mainpage'>
<h1>Welcome to the AI Interview</h1>
<p style={{ display: logpage ? "block" : "none" }}>Login to start</p>

<div style={{ display: logpage ? "none" : "block" }} className='pagemovebutton'>

<br></br><br></br><br></br>
<p>When the interview game is started, you are not able to stop it.</p>
<p>If you are ready, press the Start Game button and process the interview.</p>

<Link to="/tilegame">
        <button>Start Game</button>
      </Link>

      </div>
      <div style={{ display: logpage ? "block" : "none" }} className='form'>
        

        <h2>Login</h2>
        <input type='text' onChange={(e) => {loginID = e.target.value;}} />
        <input type='password' onChange={(e) => {loginPW = e.target.value;}}  />

        <button onClick={login}>LOGIN</button>

        <br></br><br></br><br></br>

        <h2>Register new account</h2>
        <input type='text' onChange={(e) => {ID = e.target.value;}} />
        <input type='password' onChange={(e) => {PW = e.target.value;}}  />

        <button onClick={register}>REGISTER</button>
        <br></br><br></br><br></br> 
      </div>
      </div>
    {admin ? <Link to="/login">
        <button>Adminstrater Page</button>
      </Link> : null}
        </>
    )
}



export default RegisterLogin;