    function arraysort(arr,targetArray) {
      return arr.sort((a, b) => {
        const distanceFromA = calculateDistance(a, targetArray);
        const distanceFromB = calculateDistance(b, targetArray);
        return distanceFromA - distanceFromB;
      });
    }

    function arraysortsolving(arr,targetArray,text) {
      return arr.sort((a, b) => {
        const distanceFromA = calculateDistance(a[text].split(',').map(ee => parseInt((ee))), targetArray);
        const distanceFromB = calculateDistance(b[text].split(',').map(ee => parseInt((ee))), targetArray);
        return distanceFromA - distanceFromB;
      });
    }

    function arraysortsolvinglog(arr,targetArray) {
      return arr.sort((a, b) => {
        const distanceFromA = calculateDistance(a["log"].split(',').map(pair => parseInt(pair.split(':')[1]), targetArray));
        const distanceFromB = calculateDistance(b["log"].split(',').map(pair => parseInt(pair.split(':')[1]), targetArray));
        return distanceFromA - distanceFromB;
      });
    }

    function calculateDistance(arr1, arr2) {
      let distance = 0;
      for (let i = 0; i < arr1.length; i++) {
        distance += Math.abs(arr1[i] - arr2[i]);
      }
      return distance;
    }

//data for 10 inputs in sorting 
